// Functional Imports
import { useReducer } from 'react';
import { styled } from '@mui/material/styles';
import { useEffect } from "react";
import cssNameSimplifier from '../../../utility/css-name-simplifier';
import { useHttpClientWithNavigate } from '../../../utility/http-hook';
import { useDispatch } from 'react-redux';
import { AlertSliceActions } from '../../../store/alert-slice';
import { Link } from 'react-router-dom';
import isEmpty from '../../../utility/is-empty';
import moment from 'moment';


// Component Imports
import { Table, TableBody, TableCell, tableCellClasses, TableContainer, TableHead, TableRow, Paper, Button } from '@mui/material';
import CheckIcon from '@mui/icons-material/Check';

// Style Imports
import importStyles from './TableView.module.css';

const styles = cssNameSimplifier(importStyles);

// Table style config
const StyledTableCell = styled(TableCell)(({ theme }) => ({
	[`&.${tableCellClasses.head}`]: {
		backgroundColor: theme.palette.common.black,
		color: theme.palette.common.white,
	},
	[`&.${tableCellClasses.body}`]: {
		fontSize: 14,
	},
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
	'&:nth-of-type(odd)': {
		backgroundColor: theme.palette.action.hover,
	},
	// hide last border
	'&:last-child td, &:last-child th': {
		border: 0,
	},
}));

// BookingForm Data Reducer Function
function bookingFormDataReducer(state, action){
    if(action.type === 'NEW_DATA'){
        const loadMore = action.data.length > 100
        if(loadMore) action.data.pop()
        if(isEmpty(action.data)) return { data: [...state.data], loadMore: false }
        return { data: [...state.data, ...action.data], loadMore }
    }

    if(action.type === 'RESET'){
        return state//{ data: [], loadMore: false }
    }
}

function TableView(props){
    const dispatch = useDispatch();
    const sendRequest = useHttpClientWithNavigate();
    const [bookingFromState, dispatchBookingFormState] = useReducer(bookingFormDataReducer, { data: [], loadMore: false });

    async function fetchBookingFormData(){
        const currentUrl = new URL(window.location);
        const url = props.baseURL + '/view';
        const urlWithFilters = url + currentUrl.search;
        const headers = {};

        headers['total_count'] = bookingFromState.data.length;

        try{
            const bookingFormData = await sendRequest({ url: urlWithFilters, headers });
            dispatchBookingFormState({ type: 'NEW_DATA', data: bookingFormData });
        }catch( err ){
            // navigate(url);
            dispatch( AlertSliceActions.showAlert({ message: err.msg ?? 'Failed to fetch booking form data!', title: 'Request Failed!', severity: 'error' }) )
        }
    }

    useEffect(() => {
        (async () => await fetchBookingFormData() )();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <div className={styles.tableContainer}>
            <TableContainer component={Paper}>
                <Table sx={{ minWidth: 700 }} aria-label="customized table">
                    <TableHead>
                        <TableRow>
                            <StyledTableCell align="left">Index</StyledTableCell>
                            <StyledTableCell>Full Name</StyledTableCell>
                            <StyledTableCell align="center">Email</StyledTableCell>
                            <StyledTableCell align="center">Mobile Number</StyledTableCell>
                            <StyledTableCell align="center">Project Name</StyledTableCell>
                            <StyledTableCell align="center">Project Unit Name</StyledTableCell>
                            <StyledTableCell align="center">Status</StyledTableCell>
                            <StyledTableCell align="center">Submitted</StyledTableCell>
                            <StyledTableCell align="center">Created Date Time</StyledTableCell>
                            <StyledTableCell align="center">Created By</StyledTableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {bookingFromState.data.map((row, index) => (
                            <StyledTableRow key={row.id} className={styles.dataRow}>
                                <StyledTableCell align="center">
                                    {++index}
                                </StyledTableCell>

                                <StyledTableCell component="th" scope="row">
                                    <Link to={`${props.baseURL}/${row.id}/view`}>{row.fullName}</Link>
                                </StyledTableCell>
                                
                                <StyledTableCell align="center">
                                    {row.emailId}
                                </StyledTableCell>
                                
                                <StyledTableCell align="center">
                                    {row.mobileNumber}
                                </StyledTableCell>
                                
                                <StyledTableCell align="center">
                                    {row.Project.name}
                                </StyledTableCell>
                                
                                <StyledTableCell align="center">
                                    {row.ProjectUnit.name}
                                </StyledTableCell>
                                
                                <StyledTableCell align="center">
                                    {row.status}
                                </StyledTableCell>
                                
                                <StyledTableCell align="center">
                                    {row.submittedByUser && <CheckIcon />}
                                </StyledTableCell>
                                
                                <StyledTableCell align="center">
                                    {( moment(row.createdAt) !== 'Invalid Date') && moment(row.createdAt).format('DD/MM/YYYY HH:MM A')}
                                </StyledTableCell>
                                
                                <StyledTableCell align="center">
                                    <Link to={`/profile/${row.BookingFormCreatedBy.id}/info`}> {row.BookingFormCreatedBy.name}</Link>
                                </StyledTableCell>
                            </StyledTableRow>
                        ))}
                    </TableBody>
                </Table>

                {isEmpty(bookingFromState.data) && <h3 className={styles.noDataFound}>No Data Found</h3>}

            </TableContainer>

            {
                bookingFromState.loadMore && 
                <Paper className={styles.loadMoreContainer} onClick={fetchBookingFormData}>
                    <Button variant="outlined">Load More</Button>
                </Paper>
            }
        </div> 
    );
}

export default TableView;