// Functional Imports
import { useEffect, useState } from "react";
import cssNameSimplifier from "../../../utility/css-name-simplifier";
import { useDispatch } from "react-redux";
import { useHttpClientWithNavigate } from "../../../utility/http-hook";
import { AlertSliceActions } from '../../../store/alert-slice';
import isEmpty from "../../../utility/is-empty";

// Components Imports
import { Typography, TextField, Button, Divider, Autocomplete, styled, Alert } from '@mui/material';
import FetchDataFromLeadSquare from "./FetchDataFromLeadsquare";
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import FileIcon from '@mui/icons-material/InsertDriveFile';
import { LoaderSliceActions } from "../../../store/loader-slice";
import PopupModel from "../../../componets/PopupModel/PopupModel";
import SourceInfoHandler from "./SourceInfoHandler";

// Styled Imports
import importedStyles from './CreateNew.module.css';

const VisuallyHiddenInput = styled('input')({
    clip: 'rect(0 0 0 0)',
    clipPath: 'inset(50%)',
    height: 1,
    overflow: 'hidden',
    position: 'absolute',
    bottom: 0,
    left: 0,
    whiteSpace: 'nowrap',
    width: 1,
});

const STYLES = cssNameSimplifier(importedStyles);

function CreateNew(props) {
    
    const dispatch                          = useDispatch();
    const sendRequest                       = useHttpClientWithNavigate();
    const [projectState, setProjectState]   = useState({ projects: [], projectUnits: [] });
    const [userData, setUserData]           = useState({ fullName: '', emailId: '', mobileNumber: '', project: '', projectUnit: '', source: '', subSource: '', channelPartner: '', priceSheet: {} });
    
    
    useEffect(() => {
        // Open is set to false do not make a callout
        if(!(!!props.open)) return;

        // fetching projects data
        (async () => {
            try {
                const url = '/projects/list-all';
                const projectsData = await sendRequest({ url });
                setProjectState(prev => ({ ...prev, projects: projectsData.map(prj => ({ ...prj, label: prj.name })) }))
            } catch (err) {
                dispatch(AlertSliceActions.showAlert({ message: err.msg ?? 'Failed to fetch projects data!', title: 'Request Failed!', severity: 'error' }))
            }
        })()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.open]);

    // Change in Project triggers a callout to fetch the related units of the selected project
    useEffect(() => {
        // fetching projects unit data
        (async () => {
            try {
                if (!userData?.project?.id) return;

                const url = `/projects/${userData.project.id}/fetch-units`;
                const projectUnitsData = await sendRequest({ url });
                setProjectState(prev => ({ 
                    ...prev, 
                    projectUnits: projectUnitsData.map(prjUnit => ({ 
                        ...prjUnit, 
                        // label: `${prjUnit.name} | ${prjUnit.type} | ${prjUnit.area}` 
                        label: prjUnit.name 
                    })) 
                }))
            } catch (err) {
                dispatch(AlertSliceActions.showAlert({ message: err.msg ?? 'Failed to fetch projects unit data!', title: 'Request Failed!', severity: 'error' }))
            }
        })()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [userData.project])

    function projectChangeHandler(_, val) {
        setUserData(prev => ({ ...prev, project: val, projectUnit: null }));
        if (!val) setProjectState(prev => ({ ...prev, projectUnits: [] }))
    }

    function handelFetchedUserData(data){
        const fullName          = ((!isEmpty(data.FirstName)? data.FirstName : '') + ' ' + 
                                    (!isEmpty(data.LastName)? data.LastName : '')).trim();

        const emailId           = !isEmpty(data.EmailAddress)               ? data.EmailAddress : '';
        const mobileNumber      = !isEmpty(data.Phone)                      ? data.Phone : '';
        const project           = !isEmpty(data.mx_Project_Name)            ? data.mx_Project_Name : '';
        const source            = !isEmpty(data.Source)                     ? data.Source : '';
        const subSource         = !isEmpty(data.mx_Sub_Source)              ? data.mx_Sub_Source : '';
        const channelPartner    = !isEmpty(data.mx_Channel_Partner_Name)    ? data.mx_Channel_Partner_Name : '';
        const projectUnit       = '';
        const priceSheet        = {};

        setUserData({ fullName, emailId, mobileNumber, project, projectUnit, source, subSource, channelPartner, priceSheet });
    }

    function handelInputChange(event){
        const name = event.target.name;
        const value = event.target.value;
        const type = event.target.type;

        if(type === 'file'){
            const file = event.target.files[0]
            const type = file.type;
            const fileName = file.name;

            if (type !== 'application/pdf'){
                return dispatch(AlertSliceActions.showAlert({ message: 'Only PDF file types are supported!', title: 'Request Failed!', severity: 'error' }))
            }

            dispatch(LoaderSliceActions.setDisplayLoader({ from: 'PDF Loader', state: true }));
                
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => { 
                const data = reader.result.split(',')[1]
                setUserData(prev => ({ ...prev, priceSheet: { fileName, type, data } }))
                dispatch(LoaderSliceActions.setDisplayLoader({ from: 'PDF Loader', state: false })); 
            }

            return;
        }

        setUserData(prev => ({...prev, [name]: value}))
    }

    async function handelFormSubmission(event){
        try{
            event.preventDefault();
            const url = props.baseURL + '/create-bookingform';
            const method = 'POST';
            const headers = { 'content-type': 'application/json' };
            const body = JSON.stringify(userData);

            const response = await sendRequest({url, method, headers, body});
            
            console.log(response);
        } catch(err) {
            dispatch(AlertSliceActions.showAlert({ message: err.msg ?? 'Failed to create bookingform!', title: 'Request Failed!', severity: 'error' }))
        }
    }

    function handelModelClose(event){
        setUserData({ fullName: '', emailId: '', mobileNumber: '', project: '', projectUnit: '', source: '', subSource: '', channelPartner: '', priceSheet: '' })
        props.onCloseEvent && props.onCloseEvent()
    }

    return (
        <PopupModel 
            title="Create New Booking Form" 
            open={props.open} 
            handelModelClose={handelModelClose}
        >
            <FetchDataFromLeadSquare baseURL={props.baseURL} styles={STYLES} handelFetchedUserData={handelFetchedUserData} />

            <Divider variant='middle' sx={{ marginY: 2 }} />

            <form autoComplete="false" method="POST" action="#" onSubmit={handelFormSubmission}>
                <Typography variant="h6" component="h6" textAlign="left"  sx={{ marginBottom: 2 }}>
                    Personal Information
                </Typography>

                <div className={STYLES.inputGroup}>
                    <TextField
                        className={STYLES.input}
                        label="Full Name"
                        name="fullName"
                        value={userData.fullName}
                        onChange={handelInputChange}
                        required
                    />
                    <TextField
                        className={STYLES.input}
                        label="Email Id"
                        name="emailId"
                        value={userData.emailId}
                        onChange={handelInputChange}
                        required
                    />
                    <TextField
                        className={STYLES.input}
                        label="Mobile Number"
                        name="mobileNumber"
                        value={userData.mobileNumber}
                        onChange={handelInputChange}
                    />
                </div>

                <Divider variant='middle' sx={{ marginY: 2 }} />

                <div className={STYLES.inputContainer}>
                    <Typography variant="h6" component="h6" textAlign="left"  sx={{ marginBottom: 2 }}>
                        Project Information
                    </Typography>

                    <div className={STYLES.inputGroup}>
                        <Autocomplete
                            className={STYLES.input}
                            disablePortal
                            options={projectState.projects}
                            value={userData.project}
                            onChange={projectChangeHandler}
                            isOptionEqualToValue={(opt, val) => opt.label === val}
                            noOptionsText="Fetching Projects Data.."
                            renderInput={(params) => <TextField {...params} label="Projects" required />}
                        />

                        <Autocomplete
                            className={STYLES.input}
                            disablePortal
                            options={projectState.projectUnits}
                            value={userData.projectUnit}
                            onChange={(_, val) => setUserData(prev => ({ ...prev, projectUnit: val }))}
                            isOptionEqualToValue={(opt, val) => opt.id === val.id}
                            noOptionsText="Please select a project to show its related units"
                            renderInput={(params) => <TextField {...params} label="Project Units" required/>}
                        />

                        <Button
                            component="label"
                            role={undefined}
                            variant="outlined"
                            tabIndex={-1}
                            startIcon={<CloudUploadIcon />}
                        >
                            Upload Price Sheet
                            <VisuallyHiddenInput onChange={handelInputChange} type="file" accept="application/pdf" required/>
                        </Button>
                    </div>
                </div>

                {
                    userData.priceSheet?.data &&
                    <div className={STYLES.inputContainer}>
                        <Alert severity="info" icon={<FileIcon />} sx={{ textAlign: 'center', margin: 'auto' }} className={STYLES.input} >
                            File "{userData.priceSheet?.fileName}" has been selected!
                        </Alert>
                    </div>
                }

                <Divider variant='middle' sx={{ marginY: 2 }} />

                <div className={STYLES.inputContainer}>
                    <Typography variant="h6" component="h6" textAlign="left"  sx={{ marginBottom: 2 }}>
                        Source Information
                    </Typography>

                    <SourceInfoHandler styles={STYLES} userData={userData} setUserData={setUserData} />
                </div>

                <Divider variant='middle' sx={{ marginY: 2 }} />

                <div className={STYLES.inputGroup}>
                    <Button variant="outlined" onClick={handelModelClose}>Cancel</Button>
                    <Button variant="contained" type="submit">Create</Button>
                </div>
            </form>
        </PopupModel>
    )
}

export default CreateNew;