// Functional Imports
import { useState, useEffect, useReducer, useRef } from "react";
import cssNameSimplifier from "../../../utility/css-name-simplifier";
import { useSearchParams } from "react-router-dom";
import { useHttpClientWithNavigate } from "../../../utility/http-hook";
import { useDispatch } from "react-redux";
import { AlertSliceActions } from "../../../store/alert-slice";
import isEmpty from "../../../utility/is-empty";
import useTabAccessVerifier from "../../../utility/tab-access-verifier";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { HeaderSliceName } from "../../../store/header-slice";

// Components Import
import { Tooltip, OutlinedInput, InputAdornment, Drawer, Box, Divider, Accordion, AccordionSummary, AccordionDetails } from "@mui/material";
import { ListItem, List, ListItemText, ListItemButton, Button, IconButton, Typography, Badge, ClickAwayListener } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import CloseIcon from '@mui/icons-material/Close';
import FilterAltIcon from '@mui/icons-material/FilterAlt';
import DownloadIcon from '@mui/icons-material/Download';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import AddIcon from '@mui/icons-material/Add';
import RefreshIcon from '@mui/icons-material/Refresh';
// import ArrowRightIcon from '@mui/icons-material/ArrowRight';
import CheckIcon from '@mui/icons-material/Check';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';    
import TableView from "./TableView";
import CreateNew from "../CreateNew/CreateNew";

// Styles Import
import importStyles from './ViewAll.module.css';

const styles = cssNameSimplifier(importStyles);


function View(props) {
    const [searchState, setSearchState]     = useState(null);       // Handels showing and hiding of search box
    const [filterDisplay, setFilterDisplay] = useState(false);      // Handels showing and hiding of filter data drawer 
    const [refreshState, setRefreshState]   = useState(0);          // Used to referesh TableView data by incrementing key value
    const [filters, dispatchFilters]        = useReducer(           // Conatins filter data
        reduceFilterDataProcess, { 
            search: null, 
            status: new Set(), 
            years: new Set(), 
            projects: new Set(), 
            submittedByUser: new Set(), 
            filterStatus: { displayBadge: false, filterApplied: true } 
        }
    );
    const [projectsList, setProjectlist]    = useState([]);                     // Contains user handeled projects             
    const [filterParams]                    = useSearchParams();                // To access query params
    const dispatch                          = useDispatch()                     // Redux-Dispatch
    const sendRequest                       = useHttpClientWithNavigate();      // Used to send out a request
    const searchBoxRef                      = useRef();                         // Used to focus search box on open
    const [createNewView, setCreateNewView] = useState(false);
    const [isAllowedToAccessTab]            = useTabAccessVerifier('view');
    const navigate                          = useNavigate();
    const headerSlice                       = useSelector(state => state[HeaderSliceName]);
    const { isEditable }                    = headerSlice.appPermissions;

    if(!isAllowedToAccessTab){
        navigate('/error', { state: { status: 403, msg: 'Your not allowed to access \'View\' tab' } })
    }

    // Checking for filter params and fetching all the projects handled by user
    useEffect(() => {
        try{
            (async () => {
                const type              = 'INIT';
                const status            = filterParams.get('status');
                const years             = filterParams.get('years');
                const projects          = filterParams.get('projects');
                const submittedByUser   = filterParams.get('submittedByUser');

                const responseData      = await sendRequest({ url: props.baseURL + '/user-handled-projects'});
                setProjectlist(responseData);
                
                dispatchFilters({ type, status, years, projects, submittedByUser });
            })()
        }catch( err ){
            dispatch( AlertSliceActions.showAlert({ message: 'Failed to fetch projects list!', title: 'Request Failed!', severity: 'error' }) )
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(() => {
        if(filters.search === null) return;
        const interval = setTimeout(applyFilter, 500);
        return () => clearTimeout(interval); 
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [filters.search])

    function updateFilterCondition(filterType, value){
        dispatchFilters({ type: 'PROCESS_FILTER', filterType, value })
    }

    function applyFilter(){
        const urlWithfilters = composeFilterParams(filters);
        dispatchFilters({ type: 'FILTER_APPLIED' });
        window.history.pushState({}, '', urlWithfilters);
        setRefreshState(prev => ++prev);
    }

    if( searchState ){
        setTimeout(() => searchBoxRef.current.focus(), 100);
    }

    return (
        <>
            <div className={styles.viewContainer}>
                <div className={styles.toolBar}>
                    <div className={styles.left}>
                        <ClickAwayListener onClickAway={ () => { if(searchState) setSearchState(prev => false) }}>
                            <div className={styles.searchConatiner}>
                                <span className={styles.icon} onClick={(e) => setSearchState(prev => !!!prev)}>
                                    <IconButton>
                                        {(searchState) ? 
                                            <CloseIcon /> : 
                                            <Badge variant="dot" color="success" invisible={isEmpty(filters.search)}>
                                                <SearchIcon />
                                            </Badge>
                                        }
                                    </IconButton>
                                </span>
                                <div className={`${styles.input} ${(searchState !== null) ? (searchState) ? styles.active : styles.notActive : ''}`}>
                                    <OutlinedInput
                                        endAdornment={
                                            <InputAdornment position="end">
                                                <Tooltip title='Search will be compared against Full Name, Mobile No, Email Id and Alternate Mobile No.'>
                                                    <InfoOutlinedIcon fontSize="small" />
                                                </Tooltip>
                                            </InputAdornment>
                                        }
                                        placeholder="Search"
                                        size="small" 
                                        type="search" 
                                        autoFocus={true}
                                        inputRef={searchBoxRef}
                                        onChange={e => updateFilterCondition('search', e.target.value)}
                                    />
                                </div>
                            </div>
                        
                        </ClickAwayListener>
                    </div>

                    <div className={styles.right}>
                        <span className={styles.icon}>
                            <Tooltip title="Refresh">
                                <IconButton onClick={(e) => setRefreshState(prev => ++prev)}>
                                    <RefreshIcon />
                                </IconButton>
                            </Tooltip>
                        </span>
                        <span className={styles.icon}>
                            <Tooltip title="Filters">
                                <IconButton onClick={(event) => setFilterDisplay(true)} >
                                    <Badge variant="dot" color={(filters.filterStatus.filterApplied)?'success':'warning'} invisible={!filters.filterStatus.displayBadge}>
                                        <FilterAltIcon />
                                    </Badge>
                                </IconButton>
                            </Tooltip>
                        </span>
                        <span className={styles.icon}>
                            <Tooltip title="Download">
                                <IconButton disabled>
                                    <DownloadIcon/>
                                </IconButton>
                            </Tooltip>
                        </span>
                        {
                            isEditable && 
                            <span className={styles.icon}>
                                <Tooltip title="Create New">
                                    <IconButton onClick={e => setCreateNewView(true)}>
                                        <AddIcon />
                                    </IconButton>
                                </Tooltip>
                            </span>
                        }
                    </div>
                </div>
                <TableView baseURL={props.baseURL} key={refreshState} />
            </div>

            {/* Filter Menu / Drawer */}
            <Drawer className={styles.drawer} anchor='right' open={filterDisplay} onClose={(e) => setFilterDisplay(false)} >
                <main>
                    <Box sx={{ width: 350 }} role="presentation">
                        <List className={styles.filtersList}>
                            <ListItem>
                                <h3>Filters</h3>
                            </ListItem>

                            <Divider variant="middle" />

                            <ListItem className={styles.filter}>
                                <Accordion defaultExpanded>
                                    <AccordionSummary expandIcon={<ExpandMoreIcon />} >
                                        <Typography>Status</Typography>
                                    </AccordionSummary>
                                    <AccordionDetails>
                                        <List
                                            sx={{ width: '100%', bgcolor: 'background.paper', maxHeight: 200, overflow: 'auto' }}
                                            component="nav"
                                        >
                                            {["Approved", "Rejected", "Pending"].map(status => (
                                                <ListItemButton key={status} onClick={e => updateFilterCondition('status', status)}>
                                                    <ListItemText primary={status} />
                                                    {filters.status?.has(status) && <CheckIcon />}
                                                </ListItemButton>
                                            ))}
                                        </List>
                                    </AccordionDetails>
                                </Accordion>
                            </ListItem>

                            <ListItem className={styles.filter}>
                                <Accordion defaultExpanded={filters.years.size > 0}>
                                    <AccordionSummary expandIcon={<ExpandMoreIcon />} >
                                        <Typography>Years</Typography>
                                    </AccordionSummary>
                                    <AccordionDetails>
                                        <List
                                            sx={{ width: '100%', bgcolor: 'background.paper', maxHeight: 400, overflow: 'auto' }}
                                            component="nav"
                                        >
                                            {[...Array(( new Date().getFullYear() + 1) - 1995)].map((_,index) => {
                                                const year = new Date().getFullYear() - index;
                                                return (
                                                    <ListItemButton key={year} onClick={e => updateFilterCondition('years', `${year}`)}>
                                                        <ListItemText primary={year} />
                                                        {filters.years?.has(`${year}`) && <CheckIcon />}
                                                    </ListItemButton>
                                                )
                                            })}
                                        </List>
                                    </AccordionDetails>
                                </Accordion>
                            </ListItem>

                            <ListItem className={styles.filter}>
                                <Accordion disabled={projectsList.length <= 1} defaultExpanded={filters.projects.size > 1}>
                                    <AccordionSummary
                                        expandIcon={<ExpandMoreIcon />}
                                    >
                                        <Typography>Projects</Typography>
                                    </AccordionSummary>
                                    <AccordionDetails>
                                        <List
                                            sx={{ width: '100%', bgcolor: 'background.paper', maxHeight: 350, overflow: 'auto' }}
                                            component="nav"
                                        >
                                            {projectsList.map(project => (
                                                <ListItemButton key={project.id} disabled={projectsList.length <= 1}  onClick={e => updateFilterCondition('projects', project.id)} >
                                                    <ListItemText primary={project.name} />
                                                    {filters.projects?.has(project.id) && <CheckIcon />}
                                                </ListItemButton>
                                            ))}
                                        </List>
                                    </AccordionDetails>
                                </Accordion>
                            </ListItem>

                            <ListItem className={styles.filter}>
                                <Accordion defaultExpanded={filters.submittedByUser.size > 0}>
                                    <AccordionSummary
                                        expandIcon={<ExpandMoreIcon />}
                                    >
                                        <Typography>Submitted By User</Typography>
                                    </AccordionSummary>
                                    <AccordionDetails>
                                        <List
                                            sx={{ width: '100%', bgcolor: 'background.paper', maxHeight: 200, overflow: 'auto' }}
                                            component="nav"
                                        >
                                            {[{ key: "Submitted", value: 'true' }, { key: "Not Submited", value: 'false' }].map(submitted => (
                                                <ListItemButton key={submitted.key} onClick={e => updateFilterCondition('submittedByUser', submitted.value )}>
                                                    <ListItemText primary={submitted.key} />
                                                    {filters.submittedByUser?.has(submitted.value) && <CheckIcon />}
                                                </ListItemButton>
                                            ))}
                                        </List>
                                    </AccordionDetails>
                                </Accordion>
                            </ListItem>

                        </List>
                    </Box>
                </main>

                <footer>
                    <Divider />
                    <div className={styles.filterActions}>
                        <Button variant="outlined" onClick={e => dispatchFilters({type: 'RESET'})}>Reset</Button>
                        <Button variant="contained" onClick={applyFilter} disabled={filters.filterStatus.filterApplied}>Apply</Button>
                    </div>
                </footer>
            </Drawer>

            <CreateNew baseURL={props.baseURL} open={createNewView} onCloseEvent={e => setCreateNewView(false)} />

            {/* Main Menu */}
            {/* <StyledMenuList
                anchorEl={uiFilterState.mainMenuAnchor}
                open={Boolean(uiFilterState.mainMenuAnchor)}
                onClose={e => dispatchUIFilterState({ type: 'CLOSE_ALL_MENU' })}
            >
                <MenuItem
                    disableRipple
                    onClick={(event) => dispatchUIFilterState({ type: 'STATUS_MENU_EVENT', event })}
                >
                    Status
                    <ArrowRightIcon />
                </MenuItem>
                <MenuItem onClick={handleClose} disableRipple>
                    Year
                    <ArrowRightIcon />
                </MenuItem>
                <MenuItem onClick={handleClose} disableRipple>
                    Project
                    <ArrowRightIcon />
                </MenuItem>
                <MenuItem onClick={handleClose} disableRipple>
                    <Typography variant="inherit" noWrap>
                        Submitted By User
                    </Typography>
                    <ArrowRightIcon />
                </MenuItem>
            </StyledMenuList> */}

            {/* Status Menu */}
            {/* <StyledMenuList
                anchorEl={uiFilterState.statusMenuAnchor}
                open={Boolean(uiFilterState.statusMenuAnchor)}
                onClose={e => dispatchUIFilterState({ type: 'CLOSE_ALL_MENU' })}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
            >
                <MenuItem>
                    <CheckIcon />
                    Approved
                </MenuItem>
                <MenuItem>
                    <CheckIcon />
                    Rejected
                </MenuItem>
                <MenuItem>
                    <CheckIcon />
                    Pending
                </MenuItem>
            </StyledMenuList> */}
        </>
    );
}

function reduceFilterDataProcess(state, action){
    const newState = {...state};

    if(action.type === 'INIT'){
        if(action.search){
            newState.search = action.search.trim();
        }

        if(action.status){
            newState.status = new Set(action.status.split(';').map(value => value.trim()));
        }

        if(action.years){
            newState.years = new Set(action.years.split(';').map(value => value.trim()));
        }

        if(action.projects){
            newState.projects = new Set(action.projects.split(';').map(value => value.trim()));
        }

        if(action.submittedByUser){
            newState.submittedByUser = new Set(action.submittedByUser.split(';').map(value => value.trim()));
        }
    }

    if(action.type === 'PROCESS_FILTER'){
        if( action.filterType === 'search' ){
            newState[action.filterType] = action.value
        } else {
            if( newState[action.filterType].has(action.value) ){
                newState[action.filterType].delete(action.value);
            } else {
                newState[action.filterType].add(action.value);
            }

            newState.filterStatus.filterApplied = false;
        }
    }

    if(action.type === 'RESET'){
        return { 
            search: state.search, 
            status: new Set(),
            years: new Set(), 
            projects: new Set(), 
            submittedByUser: new Set(), 
            filterStatus: { 
                displayBadge: false, 
                filterApplied: false 
            } 
        };
    }

    if(action.type === 'FILTER_APPLIED'){
        newState.filterStatus.filterApplied = true;
    }

    newState.filterStatus.displayBadge = newState.status.size > 0 || newState.years.size > 0 || newState.projects.size > 0 || newState.submittedByUser.size > 0

    return newState;
}

function composeFilterParams(filterData){
    const currentURL = new URL(window.location);
    const newURLWIthParams = new URL(currentURL.origin + currentURL.pathname);

    Object.keys(filterData).forEach( filterName => {
        if('filterStatus' === filterName) return;
        if( filterData[filterName] === null || filterData[filterName].size <= 0 ) return;
        if( 'search' === filterName && isEmpty( filterData[filterName] ) ) return;

        const filterParam = ('search' === filterName)? filterData[filterName] : [...filterData[filterName]].join(';');

        newURLWIthParams.searchParams.set(filterName, filterParam);
    });

    return newURLWIthParams;
}

export default View;