// Functional Imports
import { useState, useReducer, useEffect } from 'react';
import isEmpty from '../../../utility/is-empty';
import cssNameSimplifier from '../../../utility/css-name-simplifier';
import { useDispatch } from 'react-redux';
import { useHttpClientWithNavigate } from '../../../utility/http-hook';
import { AlertSliceActions } from '../../../store/alert-slice';

// Components Imports
import { Typography, TextField, IconButton, Tooltip, ToggleButtonGroup, ToggleButton } from '@mui/material';
import CloudSyncIcon from '@mui/icons-material/CloudSync';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';

// Style Imports0
import viewAllStyles from '../ViewAll/ViewAll.module.css';

const TOOLBAR_STYLES = cssNameSimplifier(viewAllStyles);

function userDataReducer(state, action){
    const newState = JSON.parse(JSON.stringify(state));

    if(action.type === 'NEW'){
        if(action.data.length > 1){
            newState.dataList           = action.data;          
            newState.currentDataIndex   = 0;
            newState.showIterator       = true;    
            newState.totalDataLength    = action.data.length; 
            return newState;
        }

        newState.dataList           = action.data;          
        newState.currentDataIndex   = 0;
        newState.showIterator       = false;    
        newState.totalDataLength    = action.data.length; 
        return newState;
    }

    if(action.type === 'NEXT'){
        if((newState.currentDataIndex + 1) >= newState.totalDataLength){
            newState.currentDataIndex = 0;
            return newState
        }

        ++newState.currentDataIndex;
        return newState
    }

    if(action.type === 'PREV'){
        if((newState.currentDataIndex - 1) < 0){
            newState.currentDataIndex = newState.totalDataLength - 1;
            return newState
        }

        --newState.currentDataIndex;
        return newState
    }
}

function FetchDataFromLeadSquare(props){
    const styles                = props.styles;
    const dispatch              = useDispatch();
    const sendRequest           = useHttpClientWithNavigate();
    const [fetchBy, setFetchBy] = useState('phone');
    const [userData, userDataDispatcher] = useReducer(userDataReducer, { 
        dataList:           null, 
        currentDataIndex:   null, 
        showIterator:       false, 
        totalDataLength:    null 
    })

    // Propogate leadsquare data to parent comp (CreateNew) on currentDataIndex change 
    useEffect(() => {
        if(userData.currentDataIndex === null || userData.currentDataIndex === 0) return;
        props.handelFetchedUserData(userData.dataList[userData.currentDataIndex]);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [userData.currentDataIndex])


    async function handelFormSubmission(event){
        try{
            event.preventDefault();
            const userinfo = event.target.elements.userInfo.value;
            const url = `${props.baseURL}/get-leadsquare-data?getby=${fetchBy}&userinfo=${userinfo}`;

            if(isEmpty(userinfo)){
                return dispatch(AlertSliceActions.showAlert({ message: 'User info cannot be empty', title: 'Invalid Request!', severity: 'warning' }))
            } 

            const response = await sendRequest({url});

            if(isEmpty(response)){
                return dispatch(AlertSliceActions.showAlert({ message: '', title: 'No Records Found!', severity: 'error' }))
            }

            userDataDispatcher({ type: 'NEW', data: response })
            props.handelFetchedUserData(response[0]);
        } catch( err ){
            dispatch(AlertSliceActions.showAlert({ message: err.msg ?? 'Failed to fetch user data!', title: 'Request Failed!', severity: 'error' }))
        }
    }

    async function handelDataIteration(type){
        userDataDispatcher({ type });
    }

    return (
        <form action='#' method="POST" autoComplete="off" onSubmit={handelFormSubmission}>
            <Typography variant="h6" component="h6" textAlign="left"  sx={{ marginBottom: 2 }}>
                Fetch Data From Leadsquare 
                <Typography variant="caption" display="inline-block">
                    &nbsp;(optional)
                </Typography>
            </Typography>
            <div className={styles.inputGroup}>
                <ToggleButtonGroup
                    color="primary"
                    exclusive
                    value={fetchBy}
                    onChange={(_, newFetchBy) => setFetchBy(newFetchBy)}
                >
                <ToggleButton value="phone">Phone</ToggleButton>
                <ToggleButton value="emailaddress">Email</ToggleButton>
                <ToggleButton value="id">Leadsquare Id</ToggleButton>
                </ToggleButtonGroup>
            </div>

            <div className={styles.inputGroup} style={{ marginTop: 16 }}>
                <TextField
                    className={styles.input}
                    label="User Info"
                    name="userInfo"
                    required
                />

                <span className={TOOLBAR_STYLES.icon} style={{ backgroundColor: 'transparent'}}>
                    <Tooltip title="Sync Data">
                        <IconButton type="submit" sx={{ bgcolor: '#5c5c5c14' }}  size='large'>
                            <CloudSyncIcon />
                        </IconButton>
                    </Tooltip>
                </span>
            </div>

            {
                userData.showIterator && (
                    <>
                        <Typography variant="body2" component="p" textAlign="center" color="tomato"  sx={{ marginY: 2 }}>
                            Multiple user records were discovered! Use the buttons below to navigate the data and find the relevant information.
                        </Typography>
                        <div className={`${styles.inputGroup} ${styles.fetchedDataIterator}`}>

                            <span className={TOOLBAR_STYLES.icon} style={{ backgroundColor: 'transparent'}}>
                                <Tooltip title="View Previous Data">
                                    <IconButton size='large' sx={{ bgcolor: '#5c5c5c14' }} onClick={e => handelDataIteration('PREV')} >
                                        <ChevronLeftIcon />
                                    </IconButton>
                                </Tooltip>
                            </span>

                            <Typography variant="overline" component="p" textAlign="center" display="inline" sx={{ marginY: 2 }}>
                                {userData.currentDataIndex + 1} of {userData.totalDataLength}  
                            </Typography>

                            <span className={TOOLBAR_STYLES.icon} style={{ backgroundColor: 'transparent'}}>
                                <Tooltip title="View Next Data">
                                    <IconButton size='large' sx={{ bgcolor: '#5c5c5c14' }} onClick={e => handelDataIteration('NEXT')}>
                                        <ChevronRightIcon />
                                    </IconButton>
                                </Tooltip>
                            </span>
                        </div>
                    </>
                )
            }
        </form>
    );
}

export default FetchDataFromLeadSquare;