// Functional imports
import { useState, useMemo } from "react";

// Components Import
import PopupModel from "../../../componets/PopupModel/PopupModel";
import { Link, IconButton, Tooltip } from "@mui/material";
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import DoneIcon from '@mui/icons-material/Done';

// Style Imports


function CreateUserShareableLink(props){
    const [copyIconState, setCopyIconState] = useState({ icon: <ContentCopyIcon />, copy: true, title: 'Copy Link' });

    const userBookingFormLink = useMemo(() => {
        const url = new URL(window.location);
        // return url.protocol + '//' + url.host + '/U/boooking-form/' + url.pathname.split('/')[2] + '/email-verify'
        return 'http://localhost:3001?f=' + url.pathname.split('/')[2];
    }, []);


    function handelModelClose(){
        props.handelModelClose && props.handelModelClose();
    }

    function copyLink(){
        if(!copyIconState.copy) return;

        navigator.clipboard.writeText(userBookingFormLink);
        setCopyIconState({ icon: <DoneIcon />, copy: false, title: 'Copied!' });

        const timeout = setTimeout(() => {
            setCopyIconState({ icon: <ContentCopyIcon />, copy: true, title: 'Copy Link' });
            clearTimeout(timeout)
        }, 5000);
    }

    return (
        <PopupModel title="Shareable URL" handelModelClose={handelModelClose} open={props.open}>
            <Link 
                sx={{ display: 'block', textAlign: 'center', marginY: 2 }} 
                target="_blank" 
                rel="noreferrer" 
                href={userBookingFormLink}
            >
                {userBookingFormLink}
            </Link>
        
            <div style={{ maxWidth: 'fit-content', marginInline: 'auto' }}>
                <Tooltip title={copyIconState.title} >
                    <IconButton onClick={copyLink}>
                        {copyIconState.icon}
                    </IconButton>
                </Tooltip>
            </div>
        </PopupModel>
    )
}

export default CreateUserShareableLink;