// Functional Imports
import { useState } from "react";
import { useHttpClientWithNavigate } from "../../../utility/http-hook";
import { useDispatch, useSelector } from 'react-redux';
import { HeaderSliceName } from "../../../store/header-slice";
import { AlertSliceActions } from '../../../store/alert-slice';
import { useParams } from "react-router-dom";

// Component Imports
import { Button, IconButton, Modal, TextField } from "@mui/material";
import DoubleArrowIcon from '@mui/icons-material/DoubleArrow';
import SettingsBackupRestoreIcon from '@mui/icons-material/SettingsBackupRestore';
import DoNotDisturbAltIcon from '@mui/icons-material/DoNotDisturbAlt';
import TaskAltIcon from '@mui/icons-material/TaskAlt';
import CloseIcon from '@mui/icons-material/Close';


// Styles Import

function ApprovalsHandler(props){
    const styles = props.styles;
    const sendRequest = useHttpClientWithNavigate();
    const [status, setStatus] = useState();
    const [note, setNote] = useState();
    const dispatch = useDispatch();
    const { bookingFormId } = useParams();  
    const headerSlice = useSelector(state => state[HeaderSliceName]);
    const employeeRole = headerSlice.employee.role;

    async function submitApprovals(statusReq){
        closeModal();
        try {
            const url = props.baseURL + '/' + bookingFormId + '/bookingform-processing';
            const method = 'POST';
            const headers = { "Content-Type": "application/json" };
            
            if((statusReq === 'RE_PROCESS' || statusReq === 'REJECT') && !status) return setStatus(statusReq);

            const body = JSON.stringify({ status: statusReq, note });

            const response = await sendRequest({ url, method, headers, body });

            props.setReloadComp(prev => !prev);
            dispatch(AlertSliceActions.showAlert({ message: response.msg || 'Request processed', title: 'Request Submitted', severity: 'success' }));
        } catch (err) {
            if(err.status === 412){
                return dispatch(AlertSliceActions.showAlert({ message: err.msg ?? 'Failed to submit request!', title: 'Request Failed!', severity: 'warning', autoclose: false }))
            }

            dispatch(AlertSliceActions.showAlert({ message: err.msg ?? 'Failed to submit request!', title: 'Request Failed!', severity: 'error' }))
        }
    }

    function closeModal(){
        setStatus();
        setNote();
    }

    return (props.isApprover || props.isApprover === false) && (
        <>
            <div className={`${styles.container} ${styles.bfActions}`}>
                <section>
                    <div className={styles.headerInformation}>
                        <p>
                            <IconButton disableFocusRipple={true} disableRipple={true}>
                                <DoubleArrowIcon />
                            </IconButton>
                            Form Actions
                        </p>
                    </div>
                    <div className={styles.data}>
                        {
                            employeeRole !== 'Project Head' &&
                            <Button
                                variant="outlined"
                                endIcon={<SettingsBackupRestoreIcon />}
                                disabled={!props.isApprover}
                                onClick={e => submitApprovals("RE_PROCESS")}
                            >
                                Re-Process
                            </Button>
                        }
                        <Button
                            variant="outlined"
                            endIcon={<DoNotDisturbAltIcon />}
                            disabled={!props.isApprover}
                            onClick={e => submitApprovals("REJECT")}
                        >
                            Reject
                        </Button>

                        <Button
                            variant="contained"
                            endIcon={<TaskAltIcon />}
                            disabled={!props.isApprover}
                            onClick={e => submitApprovals("APPROVE")}
                        >
                            Approve
                        </Button>
                    </div>
                </section>
            </div>
                        
            {/* Notes Modal */}
            <Modal open={Boolean(status)} keepMounted={false} onClose={closeModal} >
                <div className={styles.container + ' ' + styles.modalNoteConatiner}>
                    <CloseIcon className={styles.closeIcon} onClick={closeModal} />

                    <div className={styles.noteInputsContainer}>
                        <p>
                            <strong>Would you like to add a note for {(status === 'RE_PROCESS')? 're-processing' : 'rejection'}?</strong>
                        </p>
                        <TextField 
                            className={styles.noteTextField}
                            placeholder="Add a note"  
                            name="note"
                            value={note}
                            fullWidth
                            multiline 
                            minRows={2}
                            maxRows={15}
                            onChange={e => setNote(e.target.value)}
                        />
 
                        <div className={styles.noteActionsContainer}>
                            <Button data-mode="MODAL" data-type="CLOSE" onClick={closeModal}>Cancel</Button>
                            <Button 
                                variant="contained"
                                onClick={e => submitApprovals(status)}
                            >
                                Submit
                            </Button>
                        </div>
                    </div>
                </div>
            </Modal>
        </>
    )
}

export default ApprovalsHandler;