// Functional Imports
import { useEffect, useState } from "react";
import isEmpty from "../../../utility/is-empty";
import { useHttpClientWithNavigate } from "../../../utility/http-hook";
import { useDispatch, useSelector } from 'react-redux';
import { AlertSliceActions } from '../../../store/alert-slice';
import { HeaderSliceName } from "../../../store/header-slice";
import { useParams } from "react-router-dom";

// Component Imports
import { IconButton, MenuItem, Menu, styled, alpha, Modal, TextField, FormControlLabel, Button, Checkbox, Divider } from "@mui/material";
import MoreVertIcon from '@mui/icons-material/MoreVert';
import AddIcon from '@mui/icons-material/Add';
import DeleteIcon from '@mui/icons-material/Delete';
import EditNoteIcon from '@mui/icons-material/EditNote';
import DoubleArrowIcon from '@mui/icons-material/DoubleArrow';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import PendingActionsIcon from '@mui/icons-material/PendingActions';
import CloseIcon from '@mui/icons-material/Close';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import CancelIcon from '@mui/icons-material/Cancel';

const StyledMenu = styled(
    props => (
    <Menu
        elevation={0}
        anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right',
        }}
        transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
        }}
        {...props}
    />
    ))(({ theme }) => ({
        '& .MuiPaper-root': {
            borderRadius: 6,
            marginTop: theme.spacing(1),
            minWidth: 180,
            color:
                theme.palette.mode === 'light' ? 'rgb(55, 65, 81)' : theme.palette.grey[300],
            boxShadow:
                'rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px',
            '& .MuiMenu-list': {
                padding: '4px 0',
            },
            '& .MuiMenuItem-root': {
                '& .MuiSvgIcon-root': {
                    fontSize: 18,
                    color: theme.palette.text.secondary,
                    marginRight: theme.spacing(1.5),
                },
                '&:active': {
                    backgroundColor: alpha(
                        theme.palette.primary.main,
                        theme.palette.action.selectedOpacity,
                    ),
                },
            },
        },
})
);

function NoteHandlerComp(props){
    const STYLES                                            = props.styles;
    const { bookingFormId }                                 = useParams();  
    const dispatch                                          = useDispatch();
    const [notesState, setNotesState]                       = useState([]);
    const [noteMenuAndModalState, setNoteMenuAndModalState] = useState({ menuAnchor: null, modalType: null });
    const [noteUserInputState, setNoteUserInputState]       = useState({ id: '', note: '', requriesApproval: false });
    const sendRequest                                       = useHttpClientWithNavigate();
    const headerSlice                                       = useSelector(state => state[HeaderSliceName].appPermissions);
    const { noteApprover }                                  = headerSlice;
    const [isEditable, setIsEditableState]                  = useState(null);

    useEffect(() => {
        setNotesState(props.bookingFormData.notes);
    }, [props.bookingFormData])

    useEffect(() => {
        setIsEditableState(props.isEditable)
    }, [props.isEditable])

    async function handelNoteProcessing(type){
        try{
            const url = props.baseURL + '/' + bookingFormId + '/note';
            const method = 'POST';
            const headers = {"Content-Type": "application/json"};
    
            if(type === 'NEW'){
                const newURL = url + '?type=NEW';
                const body = JSON.stringify({ 
                    note: noteUserInputState.note, 
                    requriesApproval: noteUserInputState.requriesApproval 
                });
                
                const response = await sendRequest({ url: newURL, method, body, headers });
    
                if(response.notes) setNotesState(response.notes)
    
                return dispatch( AlertSliceActions.showAlert({ message: 'Record created', title: 'Request Completed!', severity: 'success' }) )
            }
    
            if(type === 'EDIT'){
                const newURL = url + '?type=EDIT';
                const body = JSON.stringify({ 
                    id: noteUserInputState.id,
                    note: noteUserInputState.note, 
                    requriesApproval: noteUserInputState.requriesApproval 
                });
                
                const response = await sendRequest({ url: newURL, method, body, headers });
    
                if(response.notes) setNotesState(response.notes)
    
                return dispatch( AlertSliceActions.showAlert({ message: 'Record updated!', title: 'Request Completed!', severity: 'success' }) )
            }
    
            if(type === 'DELETE'){
                const newURL = url + '?type=DELETE';
                const body = JSON.stringify({ id: noteUserInputState.id });
                const response = await sendRequest({ url: newURL, body, method, headers });
    
                if(response.notes) setNotesState(response.notes)
                return dispatch( AlertSliceActions.showAlert({ message: 'Record deleted!', title: 'Request Completed!', severity: 'success' }) )
            }

            if(type === 'NOTE_APPROVE' || type === 'NOTE_REJECT'){
                const newURL = props.baseURL + '/' + bookingFormId + '/note-processing';
                const status = (type === 'NOTE_APPROVE')? 'APPROVE' : 'REJECT';
                const noteId = noteUserInputState.id;
                const body = JSON.stringify({ noteId, status });
                const headers = {"Content-Type": "application/json"};

                const response = await sendRequest({ url: newURL, body, method, headers });
                
                if(response.notes) setNotesState(response.notes)
                return dispatch( AlertSliceActions.showAlert({ message: response.msg || 'Record processed!', title: 'Request Completed!', severity: 'success' }) )
            }
    
            dispatch( AlertSliceActions.showAlert({ message: 'Type mismatch', title: 'Request Failed!', severity: 'warning' }) )
        } catch ( err ){
            dispatch( AlertSliceActions.showAlert({ message: err.msg ?? `Failed to process request`, title: 'Request Failed!', severity: 'error' }) )
        }
    }
    
    function handelNoteMenuAndModal({type, id, event}){
        if(type === 'MENU_OPEN'){
            const note = notesState.filter(note => note.id === id)[0];

            if(!note) return alert('Failed to find note');

            setNoteMenuAndModalState({ menuAnchor: event.currentTarget, modalType: null });
            setNoteUserInputState({ id: note.id, note: note.note, requriesApproval: !!note.approval });
        }
        
        if(type === 'MODAL_OPEN_IN_EDIT'){
            setNoteMenuAndModalState({ menuAnchor: null, modalType: 'EDIT' });
        }
        
        if(type === 'MODAL_OPEN_IN_NEW'){
            setNoteUserInputState({ id: '', note: '', requriesApproval: false });
            setNoteMenuAndModalState({ menuAnchor: null, modalType: 'NEW' });
        }

        if(type === 'CLOSE'){
            setNoteMenuAndModalState({ menuAnchor: null, modalType: null });
            setNoteUserInputState({ id: '', note: '', requriesApproval: false });
        }

        if(type === 'PROCESS_SUBMISSION'){
            handelNoteProcessing(noteMenuAndModalState.modalType);
            setNoteMenuAndModalState({ menuAnchor: null, modalType: null });
            setNoteUserInputState({ id: '', note: '', requriesApproval: false });
        }

        if(type === 'DELETE_RECORD'){
            handelNoteProcessing('DELETE');
            setNoteMenuAndModalState({ menuAnchor: null, modalType: null });
            setNoteUserInputState({ id: '', note: '', requriesApproval: false });
        }

        if(type === 'TRACK_NOTE_UPDATE'){
            const name = event.target.name;
            const value = (name === 'note')? event.target.value : event.target.checked;
            setNoteUserInputState(prev => ({ ...prev, [name]: value }))
        }

        if(type === 'NOTE_APPROVE' || type === 'NOTE_REJECT'){
            handelNoteProcessing(type);
            setNoteMenuAndModalState({ menuAnchor: null, modalType: null });
        }
    }

    return (
        <>
            <div className={`${STYLES.container} ${STYLES.bfNotes}`}>
                <section>
                    <div className={STYLES.headerInformation}>
                        <p>
                            <IconButton disableFocusRipple={true} disableRipple={true}>
                                <DoubleArrowIcon />
                            </IconButton>
                            Notes
                        </p>
                    </div>
                    <div className={STYLES.data} style={{ justifyContent: "flex-start" }}>
                        {
                            isEmpty(notesState) && <p style={{ textAlign: 'center', width: '100%' }}>No notes found!</p>
                        }
                        { 
                            !isEmpty(notesState) && 
                            notesState.map( (note, index) => (
                                <div className={STYLES.noteContainer} key={note.id}>
                                    <div>
                                        {
                                            index > 0 && 
                                            notesState[index-1].from !== note.from && // Checks if the previous note is the same user or not
                                            <div className={STYLES.noteFrom}>
                                                <h3>{note.from}</h3> 
                                            </div>
                                        }
                                        {
                                            index <= 0 && 
                                            <div className={STYLES.noteFrom}>
                                                <h3>{note.from}</h3> 
                                            </div>
                                        }
                                        <div className={STYLES.note}>
                                            {
                                                note.approval &&
                                                <div className={STYLES.approvalInfo}>
                                                    { 
                                                        note.approval.status === null &&
                                                        <>
                                                            <PendingActionsIcon className={STYLES.icon} /> 
                                                            <p>
                                                                Approval Pending
                                                            </p>
                                                        </>
                                                    }
                                                    { 
                                                        note.approval.status === true && 
                                                        <>
                                                            <CheckCircleOutlineIcon className={STYLES.icon} /> 
                                                            <p>
                                                                Approved By {note.approval.updatedBy}
                                                            </p>
                                                        </>
                                                    }
                                                    { 
                                                        note.approval.status === false && 
                                                        <>
                                                            <HighlightOffIcon className={STYLES.icon} /> 
                                                            <p>
                                                                Rejected By {note.approval.updatedBy}
                                                            </p>
                                                        </>
                                                    }
                                                </div>
                                            }
                                            <p>{note.note}</p>    
                                        </div>
                                    </div>
                                    <div>
                                        <IconButton onClick={event => handelNoteMenuAndModal({ type: 'MENU_OPEN', id: note.id, event })}>
                                            <MoreVertIcon />
                                        </IconButton>
                                    </div>
                                </div>
                            ))
                        }

                        <StyledMenu
                            id="notes-menu"
                            anchorEl={noteMenuAndModalState.menuAnchor}
                            open={Boolean(noteMenuAndModalState.menuAnchor)}
                            onClose={e => handelNoteMenuAndModal({ type: 'CLOSE' })}
                        >
                            {
                                noteApprover && noteUserInputState.requriesApproval && [
                                    (
                                        <MenuItem onClick={e => handelNoteMenuAndModal({ type: 'NOTE_APPROVE' })} key="MenuItem_Approve">
                                            <CheckCircleIcon />
                                            Approve
                                        </MenuItem>
                                    ), (
                                        <MenuItem onClick={e => handelNoteMenuAndModal({ type: 'NOTE_REJECT' })} key="MenuItem_Reject">
                                            <CancelIcon />
                                            Reject
                                        </MenuItem>
                                    ), (
                                        <Divider  key="MenuItem_Divider"/>
                                    )
                                ]
                            }
                            <MenuItem onClick={e => handelNoteMenuAndModal({ type: 'MODAL_OPEN_IN_EDIT' })}>
                                <EditNoteIcon />
                                Edit
                            </MenuItem>
                            <MenuItem onClick={e => handelNoteMenuAndModal({ type: 'DELETE_RECORD' })}>
                                <DeleteIcon />
                                Delete
                            </MenuItem>
                        </StyledMenu>
                    </div> 
                    {
                        isEditable && (
                            <div className={STYLES.data} style={{ justifyContent: "flex-end"}}>
                                <IconButton style={{ backgroundColor: '#90caf9'}} onClick={e => handelNoteMenuAndModal({ type: 'MODAL_OPEN_IN_NEW' })}>
                                    <AddIcon sx={{ color: '#000', fontSize: 30 }} />
                                </IconButton>
                            </div>
                        )
                    }
                </section>
            </div>

            {/* Notes Modal */}
            <Modal open={Boolean(noteMenuAndModalState.modalType)} keepMounted={false} onClose={e => handelNoteMenuAndModal({type: 'CLOSE'})} >
                <div className={STYLES.container + ' ' + STYLES.modalNoteConatiner}>
                    <CloseIcon className={STYLES.closeIcon} onClick={e => handelNoteMenuAndModal({ type: 'CLOSE'})} />

                    <div className={STYLES.noteInputsContainer}>
                        <TextField 
                            className={STYLES.noteTextField}
                            label="Note"
                            placeholder="Add a note"  
                            name="note"
                            fullWidth
                            multiline 
                            minRows={2}
                            maxRows={20}
                            disabled={!isEditable}
                            value={noteUserInputState.note}
                            required
                            onChange={event => handelNoteMenuAndModal({ type: 'TRACK_NOTE_UPDATE', event })}
                        />

                        <FormControlLabel 
                            className={STYLES.inputContainer} 
                            control={<Checkbox />} 
                            label="Requires Approval?" 
                            labelPlacement="start"
                            name="requriesApproval"
                            disabled={!isEditable} 
                            sx={{ justifyContent: 'flex-end' }}
                            checked={noteUserInputState.requriesApproval}
                            onChange={event => handelNoteMenuAndModal({ type: 'TRACK_NOTE_UPDATE', event })}
                        />
                        {
                            isEditable && 
                            <div className={STYLES.noteActionsContainer}>
                                <Button data-mode="MODAL" data-type="CLOSE" onClick={e => handelNoteMenuAndModal({ type: 'CLOSE'})}>Cancel</Button>
                                <Button 
                                    variant="contained"
                                    onClick={e => handelNoteMenuAndModal({ type: 'PROCESS_SUBMISSION' })}
                                >
                                    {noteMenuAndModalState.modalType === 'EDIT'? 'Update' : 'Submit'}
                                </Button>
                            </div>
                        }
                    </div>
                </div>
            </Modal>
        </>
    );
}

export default NoteHandlerComp;
export { StyledMenu };