// Functional Imports
import { useState, useMemo } from "react";
import { useRoutes, useNavigate, Navigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { HeaderSliceActions } from "../store/header-slice";
import { useEffect, lazy, Suspense } from "react";
import { useHttpClientWithNavigate } from "../utility/http-hook";

// Component Imports
import Loading from "../componets/Loading/Loading";
import ViewAll from "../Apps/BookingForm/ViewAll/ViewAll";
import View from "../Apps/BookingForm/View/View";
import { useParams } from "react-router-dom";

// Components Imports (Lazy)
const Root = lazy(() => import("../Apps/BookingForm/Root"))

function BookingFormPages(){
    const dispatcher            = useDispatch();
    const sendRequest           = useHttpClientWithNavigate();
    const navigate              = useNavigate();
    const baseURL               = '/booking-form';
    const [router, setRouter]   = useState([]);

    const routes = useMemo(() => [
        { 
            index: true, 
            element: (
                <Suspense fallback={<Loading fallback />}>
                    <Root baseURL={baseURL}/>,
                </Suspense>
            )
        },
        { 
            path: 'view', 
            element: (
                <Suspense fallback={<Loading fallback />}>
                    <ViewAll baseURL={baseURL}/> 
                </Suspense>
            )
        },    
        { 
            path: '/:bookingFormId', 
            element: <RedirectToBookingFormView baseURL={baseURL}/> 
        },    
        { 
            path: ':bookingFormId/view', 
            element: (
                <Suspense fallback={<Loading fallback />}>
                    <View baseURL={baseURL}/> 
                </Suspense>
            )
        },    
        { 
            path: 'reports', 
            element: (
                <Suspense fallback={<Loading fallback />}>
                    <p>Will be added soon</p> 
                </Suspense>
            )
        },    
        { 
            path: '*', 
            element: (
                <Suspense fallback={<Loading fallback />}>
                    <Navigate to='/error' state={{ statusText: 'Page Not Found!', status: 404 }} />
                </Suspense>
            )
        }    
    ], []);

    useEffect(() => {

        (async () => {
            try{
                const response = await sendRequest({ url: '/apps/booking-form/app-headers' });

                if(!response) return
            
                dispatcher( HeaderSliceActions.setHeaders({ 
                    currentApp: 'Booking Form', 
                    appTabs: response.allowedtabs.map(tab => ({ ...tab, baseURL })),
                    appPermissions: response.permissions
                }) );

                setRouter(routes);
            } catch( err ){
                navigate('/error', { state: { status: err.status, data: err.msg } });
            }
        })();
        
    }, [dispatcher, navigate, routes, sendRequest]);

    ;
    
    return useRoutes(router);
}

function RedirectToBookingFormView(props){
    const { bookingFormId } = useParams();
    return <Navigate to={`${props.baseURL}/${bookingFormId}/view`}  replace={true} />
}

export default BookingFormPages;