// Functional Imports
import { useEffect, useState } from 'react';
import { AlertSliceActions } from '../../../store/alert-slice';
import { useDispatch } from 'react-redux';
import { useHttpClientWithNavigate } from '../../../utility/http-hook';
import isEmpty from '../../../utility/is-empty';

// Components Imports
import { TextField, Autocomplete } from '@mui/material';

function SourceInfoHandler(props){
    const channelPartnerLabels = ["cp", "c p", "channel partner"];
    const styles = props.styles;
    const [sourceData, setSourceData] = useState({ sourceWithSubSource: [], currentSubSourceForSource: [] });
    const dispatch = useDispatch();
    const sendRequest = useHttpClientWithNavigate();
    const [cpNameView, setCpNameViewState]  = useState(false);
    const [channelPartnerData, setChannelPartnerData] = useState([]);


    useEffect(() => {
        (async() => {
            try {
                const url = '/source/retrive-source-with-subsource';
                const sourceData = await sendRequest({ url });
                setSourceData(prev => ({ ...prev, sourceWithSubSource: sourceData }))
            } catch (err) {
                dispatch(AlertSliceActions.showAlert({ message: err.msg ?? 'Failed to fetch source data!', title: 'Request Failed!', severity: 'error' }))
            }
        })();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    // Controlling Sub Source Field and Fethcing Channel Partner Information if cp is selected as source
    useEffect(() => {
        if(isEmpty(sourceData.sourceWithSubSource)) return;

        if(isEmpty(props.userData?.source?.id)){
            setSourceData(prev => ({ ...prev, currentSubSourceForSource: [] }))
            return;
        }

        const source = sourceData.sourceWithSubSource.filter(source => source.id === props.userData.source.id);

        // Fethcing channel partner information
        if(channelPartnerLabels.includes(props.userData.source.label) && isEmpty(channelPartnerData)){
            const channelPartnerUrl = '/channel-partner/view-all';
            sendRequest({ url: channelPartnerUrl })
            .then(responseData => {
                const modifiedChannelPartner = responseData.map(cp => ({ ...cp, label: cp.name }))
                setChannelPartnerData(modifiedChannelPartner);
            })
            .catch(err => {
                dispatch(AlertSliceActions.showAlert({ message: err.msg ?? 'Failed to fetch source data!', title: 'Request Failed!', severity: 'error' }))
            })
        }

        setSourceData(prev => ({ ...prev, currentSubSourceForSource: source[0]?.subsources || null }));

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.userData.source, sourceData.sourceWithSubSource])

    useEffect(() => {
        setCpNameViewState(channelPartnerLabels.includes(props.userData.source?.label?.toLowerCase()))
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props.userData.source])

    function inputChangeHandler(key, value){
        props.setUserData(prev => ({ ...prev, [key]: value }))
    }

    return (
        <div className={styles.inputGroup}>
            <Autocomplete
                className={styles.input}
                disablePortal
                options={sourceData.sourceWithSubSource.map(source => ({ id: source.id, label: source.label }))}
                value={props.userData.source}
                onChange={(_, val) => inputChangeHandler('source', val)}
                isOptionEqualToValue={(opt, val) => opt.id === val.id}
                loadingText='Fetching source infromation'
                renderInput={(params) => <TextField {...params} label="Source" required />}
            />
            <Autocomplete
                className={styles.input}
                disablePortal
                options={sourceData.currentSubSourceForSource}
                value={props.userData.subSource}
                onChange={(_, val) => inputChangeHandler('subSource', val)}
                isOptionEqualToValue={(opt, val) => opt.id === val.id}
                noOptionsText = 'Select a source to fetch subsource information'
                loadingText = 'Fetching subsource information'
                renderInput={(params) => <TextField {...params} label="Sub Source" />}
            />
            {
                cpNameView && 
                <Autocomplete
                    className={styles.input}
                    disablePortal
                    options={channelPartnerData}
                    value={props.userData.channelpartner}
                    onChange={(_, val) => inputChangeHandler('channelpartner', val)}
                    isOptionEqualToValue={(opt, val) => opt.id === val.id}
                    renderInput={(params) => <TextField {...params} label="Channel Partner" required />}
                />
            }
        </div>
    )
}

export default SourceInfoHandler;