// Functional imports


// Components imports
import { Divider } from "@mui/material";

// Styles imports


function HeaderView(props){
    const styles = props.styles;
    const indianRupeesFormater = new Intl.NumberFormat('en-IN', { currency: 'INR' });


    return (
        <div className={styles.container + ' ' + styles.quickViewConatiner}>
            <div>
                <h3>Project</h3>
                <p>{props?.bookingFormData?.Project?.name}</p>
            </div>
            
            <Divider orientation="vertical" flexItem />

            <div>
                <h3>Project Unit</h3>
                <p>{props?.bookingFormData?.ProjectUnit?.name}</p>
            </div>

            <Divider orientation="vertical" flexItem />

            <div>
                <h3>Current Unit Price</h3>
                <p style={{ textAlign: 'center' }}>
                    {
                        (props?.bookingFormData?.currentUnitPrice)?
                            indianRupeesFormater.format(props.bookingFormData.currentUnitPrice) : '-----'
                    }
                </p>
            </div>

            <Divider orientation="vertical" flexItem />

            <div>
                <h3>Discount ()</h3>
                <p style={{ textAlign: 'center' }}>
                    {
                        (props?.bookingFormData?.discount)?
                            indianRupeesFormater.format(props.bookingFormData.discount) : '-----'
                    }
                </p>
            </div>

            <Divider orientation="vertical" flexItem />

            <div>
                <h3>After Discount ()</h3>
                <p style={{ textAlign: 'center' }}>
                    {
                        (props?.bookingFormData?.currentUnitPrice && props?.bookingFormData?.discount)?
                            indianRupeesFormater.format((props?.bookingFormData.currentUnitPrice - props?.bookingFormData.discount)) : '-----'
                    }
                </p>
            </div>

            <Divider orientation="vertical" flexItem />

            <div>
                <h3>Realization Price</h3>
                <p style={{textAlign: 'center'}}>
                    {
                        (props?.bookingFormData?.totalCost && props?.bookingFormData?.sqft)? 
                            indianRupeesFormater.format((props?.bookingFormData.totalCost / props?.bookingFormData.sqft)) : '-----'
                    }
                </p>
            </div>

        </div>
    );
}

export default HeaderView;